body {
  background-color: #002851;
  color: white;
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  overflow: hidden;
  height: 100vh;
  width: 100vw;
}


.container {
  display: flex;
  align-items:center;
  flex-direction: column;
  text-align: center;
  text-size-adjust: auto;
  width: 100vw;
  height: 100vh;
}

.time {
  font-size: 15rem;
  font-weight: 500;
}

.text {
  display: block;
  margin: auto;
  font-size: 5rem;
  text-transform: uppercase;
  font-weight: 1000;
}


@media only screen and (max-width: 800px) {
  .time {
    font-size: 15vw;
  }
  
  .text {
    font-size: 12vw;
  }
}